#title-box {
	color: #fff;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-line-pack: center;
	    align-content: center;
	-ms-flex-align: center;
	    align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    width: 16em;
    font-size: 1.5em;
    cursor: pointer;
}

h1 {
	font-size: 2em;
}

p {
	font-size: 1.2em;
}

.headerOptTxt {
	margin-right: 3px;
	margin-left: 3px;
}

.headerOptSpan {
	display: -ms-flexbox;
	display: flex;
	margin:0;
	margin-top: 1em;
}

#top-nav{
	-ms-flex-pack: center;
	    justify-content: center;
	color: #fff;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}

#logo {
	width: 3em;
	height: 3.5em;
}

header {
	background: #f2c3b0;
	width: 100%;
    padding: 0;
    overflow-x: hidden;
}

header div {
	color: #7481b0;
	margin-left: 20px;
	margin-right: 20px;
}

#header-options {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row wrap;
	    flex-flow: row wrap;
	-ms-flex-align: center;
	    align-items: center;
	width: 350px;
}


